export default{
	props:{
		msglist:{
			type:Object,
			default:{}
		}
	},
	data(){
		return{
			list:this.msglist,
			userimg:"../../../../static/login/part5_picture2.png",
			showname:false,
			showsex:false,
			shownum:true,
			checkname:"",
			sexlist:[
				{
				value:"1",
				name:"男"
			    },
				{
				value:"0",
				name:"女"
				},
			]
		
		}
	},
	created(){
	  this.list.phone=this.hidden(this.list.phone,3,4);
	  this.list.sfz=this.hidden(this.list.sfz,4,4);
	  this.hiddenrz();
	  this.hiddensex();
	},
	methods:{
		gorz(){
		 uni.navigateTo({
		     url: '../user-authentication/user-authentication'
		 });
		},
		hidden(str,frontLen,endLen) { 
										var start='';
										var str1=str.toString();
										for(var i = 0;i<frontLen;i++) {
											start+=str1[i];
										}
										var len = str1.length-frontLen-endLen;
										 var xing = '';
										 for (var i=0;i<len;i++) {
										 xing+='*';
						                  }
										  var ends=frontLen+len;
										  var end='';
										  for(var i=ends;i<str1.length;i++) {
											 end+=str1[i];
										  }
										return start+xing+end;	
								   },
		hiddenrz(){
			var rzzt=this.list.rzzt;
			if(rzzt==true){
				rzzt="实名认证";
			}else{
				rzzt="未认证";
			};
			this.list.rzzt=rzzt;
		},
		hiddensex(){
			var sex=this.list.sex;
			if(sex==1){
				sex="男"
			}else{
				sex="女"
			};
			this.list.sex=sex;
		},
		changeimg(){
			var that=this;
			uni.chooseImage({
				count:1,
				sizeType:"compressed",
				success:function(res){
					that.userimg=res.tempFilePaths;
				}
			})
		},
		changnameshow(){
			this.showname=true;
		},
	    changnameno(){
			this.showname=false;
		},
	    changnameyes(){
			this.showname=false;
			this.list.wxname=this.checkname;
		},
		changnumshow(){
			this.shownum=true;
		},
		changnumno(){
			this.shownum=false;
		},
		changnumyes(){
			this.shownum=false;
			this.list.wxnum="未定义";
			
		},
	   	changsexshow(){
			this.showsex=true;
		},
		changsexno(){
			this.showsex=false;
		},
	    changsexyes(){
			this.showsex=false;
		},
		radioChange(e){
		 console.log(e.target.value);
		}
		   
	}
}
